<template>
    <div>
        <app-rating :filterCategory="filterCategory"></app-rating>
        <app-portfolio-reuse :portfolios="portfolios" :loading="loading"></app-portfolio-reuse>
    </div>
</template>

<script>
    import appPortfolioReuse from '../../components/reuseComponents/Portfolio'
    import appRating from '../../components/category/Rating'
    export default {
        components: {
          appPortfolioReuse,
            appRating
        },
        data: () => ({
            portfolios: [],
            loading: false
        }),
        methods: {
          async filterCategory (id) {
              this.loading = true
                const results = await this.$axios.get('category/filter-category',
                    { params: { categoryId: this.$route.params.id, rating: id } })
                this.portfolios = results.data.portfolios
              setTimeout(() => {
                  this.loading = false
              }, 200)
            }
        },
        created () {
            this.filterCategory()
        }
    }
</script>
